.main-menu {
    background-color: #8c71fe;
    color: white;
    width: fit-content;
    height: fit-content;
    padding: 15px;
    border-radius: 15px;
    transition: background-color 0.5s ease;
    cursor: pointer;
}
.main-menu:hover {
    background-color: #7f63ff;
}

.root {
	height: 100%;
}

.bold,.heading {
	font-weight: 700;
}

.clearfix:after,.fl-right {
	clear: both;
}

.header_text-2,.heading-1,.heading-2,button,h1,h2 {
	text-transform: uppercase;
}

body,html {
	height: 100%;
}

body {
	margin: 0;
	padding: 0;
	font-size: 14px;
	font-family: Verdana,Arial,Helvetica,sans-serif;
	background-color: #d2d8ee;
	background-image: linear-gradient(to bottom,#e6eaf6,#d2d8ee);
	background-repeat: no-repeat;
}

.heading,.heading-1,.heading-2,.heading-3,.heading-4,.heading-5,.heading-6,h1 {
	font-family: Calibri,Helvetica,sans-serif;
}

.border-box {
	box-sizing: border-box;
}

.clearfix:after,.clearfix:before {
	content: " ";
	display: table;
}

.mb-10 {
	margin-bottom: 10px;
}

.mb-15 {
	margin-bottom: 15px;
}

.mb-20 {
	margin-bottom: 20px;
}

.heading-5,h5 {
	margin-bottom: 10px;
}

.green {
	color: green;
}

.red,p.red {
	color: red;
}

.hidden {
	display: none!important;
}

body.empty .page,iframe {
	display: none;
}

.break-all {
	word-break: break-all;
}

.heading {
	margin-top: 0;
}

*+.heading {
	margin-top: .83em;
}

.heading-1,.heading-2,.heading-3,.heading-4,.heading-5,.heading-6,h1,h2,h3,h4,h5,h6 {
	margin-top: 0;
	font-weight: 700;
}

.heading-1 {
	font-size: 28px;
}

.heading-2 {
	font-size: 24px;
}

.heading-3 {
	font-size: 22px;
}

.heading-4 {
	font-size: 18px;
}

.heading-5 {
	font-size: 16px;
}

.heading-6 {
	font-size: 14px;
}

h1 {
	font-size: 28px;
}

.header_text,h2 {
	font-size: 24px;
	font-family: Calibri,Helvetica,sans-serif;
}

h3 {
	font-family: Calibri,Helvetica,sans-serif;
	font-size: 22px;
}

h4 {
	font-family: Calibri,Helvetica,sans-serif;
	font-size: 18px;
}

h5 {
	font-family: Calibri,Helvetica,sans-serif;
	font-size: 16px;
}

h6 {
	font-family: Calibri,Helvetica,sans-serif;
	font-size: 14px;
}

p {
	font-size: 11px;
	line-height: 1.5;
	margin: 0;
}

.header_text-1,footer {
	color: #949699;
}

p.size-l {
	font-size: 12px;
}

body.empty {
	background: 0 0;
}

.page {
	min-height: 100%;
	width: 900px;
	margin: 0 auto;
	padding-bottom: 180px;
	background: #fff;
	position: relative;
	box-shadow: 0 0 30px rgba(0,0,0,.3);
	-moz-box-shadow: 0 0 30px rgba(0,0,0,.3);
	-webkit-box-shadow: 0 0 30px rgba(0,0,0,.3);
	box-sizing: border-box;
}

.dock,.header_bar,.header_content,.header_logo,.header_text,footer {
	position: absolute;
}

.dock {
	left: 0;
	right: 0;
}

header {
	height: 188px;
}

.header_bar {
	left: 0;
	right: 0;
	height: 38px;
	top: 0;
	background: #f2f2f2;
}

.header_content {
	left: 0;
	right: 0;
	height: 150px;
	top: 38px;
}

.header_logo {
	width: 138px;
	height: 50px;
	background: url(data:image/gif;base64,) no-repeat;
	left: 30px;
	top: 45px;
	cursor: pointer;
}

.header_text {
	left: 210px;
	top: 40px;
	line-height: 32px;
}

.header_text-2 {
	font-weight: 700;
}

footer {
	background: #f2f2f2;
	height: 150px;
	left: 0;
	right: 0;
	bottom: 0;
	border-top: 1px solid #d5d6d6;
	font-size: 12px;
	text-align: center;
	box-sizing: border-box;
	padding-top: 18px;
}

.content {
	padding: 20px 32px;
	border-top: 1px solid #bebfc1;
}

.fl-right {
	float: right;
}

.container.loading::before {
	width: 40px;
	height: 40px;
	animation: spin 1s linear infinite;
	border-radius: 100%;
	border-top: 2px solid #545a6a;
	border-bottom: 2px solid #d4d4db;
	border-left: 2px solid #545a6a;
	border-right: 2px solid #d4d4db;
	opacity: .7;
	content: '';
	display: block;
	margin: 50px auto;
}

.input-clone,input[type=text],input[type=password],input[type=search],select,textarea {
	margin: 0;
	padding: 4px 6px;
	font-size: 20px;
	border: 1px solid #c7c8ca;
	background: #fcfcfc;
	box-sizing: border-box;
	font-family: inherit;
}

.dataTables_length select,button {
	font-size: 14px;
}

.input-clone.error,input[type=text].error,input[type=password].error,input[type=search].error,select.error,textarea.error {
	box-shadow: 0 0 1px 1px #f1604f;
	border-color: #ED3520;
	outline-color: #ED3520;
}

.input-clone[disabled],input[type=text][disabled],input[type=password][disabled],input[type=search][disabled],select[disabled],textarea[disabled] {
	background: #D8D8D8;
}

button {
	border: none;
	background: #0071bb;
	color: #fff;
	font-weight: 700;
	padding: 14px 20px;
	outline: 0;
	cursor: pointer;
}

button[disabled] {
	cursor: default;
	background: rgba(0,113,187,.7);
	color: rgba(255,255,255,.7);
}

button:hover:not([disabled]) {
	background: #0080d4;
}

button:active:not([disabled]) {
	background: #0062a2;
}

button.btn-small {
	font-size: 11px;
	padding: 8px 10px;
}

a,button.btn-m {
	font-size: 12px;
}

button.btn-m {
	padding: 9px 14px;
}

button.btn-light {
	color: #585858;
	background: #E4E4E4;
}

button.btn-light:hover:not([disabled]) {
	background: #f1f1f1;
}

button.btn-light:active:not([disabled]) {
	background: #d7d7d7;
}

a {
	color: #0071bb;
}

a:hover {
	color: #00436e;
}

@keyframes spin {
	0% {
		transform: rotate(0);
		-webkit-transform: rotate(0);
		-moz-transform: rotate(0);
		-o-transform: rotate(0);
	}

	100% {
		transform: rotate(359deg);
		-webkit-transform: rotate(359deg);
		-moz-transform: rotate(359deg);
		-o-transform: rotate(359deg);
	}
}

.table {
	width: 100%;
	table-layout: fixed;
	border-collapse: collapse;
	font-size: 13px;
}

.table td,.table th {
	border: 1px solid #C2C2C2;
	text-align: center;
	vertical-align: center;
}

.table tfoot td,.table th {
	font-weight: 700;
	padding: 8px 11px;
	background-color: #dae1f7;
}

.table tfoot td a,.table th a {
	font-weight: 400;
	font-size: 11px;
}

.table .sub-head th {
	font-weight: 400;
}

.table td {
	padding: 11px;
}

.table tr.odd {
	background: #f2f2f2;
}

.table tr:hover td:not([rowspan]) {
	background: #dadada;
}

.table .width-220 {
	width: 220px;
}

.table .width-180 {
	width: 180px;
}

.table .width-120 {
	width: 120px;
}

.table .width-110 {
	width: 110px;
}

.table .width-80 {
	width: 80px;
}

.table .width-60 {
	width: 60px;
}

.table .width-40 {
	width: 40px;
}

.table .width-20 {
	width: 20px;
}

.table td.align-right {
	text-align: right;
}

.kege-item td,.notice,.table td.align-left,.table td.input-cell {
	text-align: left;
}

.table td.no-padding {
	padding-top: 0;
	padding-bottom: 0;
}

.table td.input-cell {
	padding: 0;
}

.table td.input-cell input {
	width: 100%;
	box-sizing: border-box;
	padding: 5px;
	height: 30px;
	line-height: 30px;
	background: 0 0;
	border: none;
	font-size: 13px;
}

.table_cells-s td {
	padding: 5px;
}

.kege-item>thead {
	display: none;
}

.kege-item td {
	height: 14px;
}

.table-subscript {
	text-align: right;
	font-style: italic;
	font-size: 12px;
}

.table-container {
	position: relative;
	overflow: auto;
	width: 100%;
	min-height: 100px;
	text-align: center;
}

.table-container.loading::after,.table-container.loading::before {
	content: '';
	position: absolute;
	left: 0;
	right: 0;
}

.table-container.loading::before {
	top: 0;
	bottom: 0;
	background: rgba(255,255,255,.5);
}

.table-container.loading::after {
	width: 40px;
	height: 40px;
	animation: spin 1s linear infinite;
	border-radius: 100%;
	border-top: 2px solid #545a6a;
	border-bottom: 2px solid #d4d4db;
	border-left: 2px solid #545a6a;
	border-right: 2px solid #d4d4db;
	opacity: .7;
	display: block;
	margin: 0 auto;
	top: 40%;
}

.dataTables_length {
	margin-bottom: 10px;
}

.account-block {
	float: right;
	font-size: 22px;
}

.account-block a {
	font-size: 24px;
}

.loader {
	display: inline-block;
}

.loader_bar {
	background-color: #FFF;
	width: 5px;
	height: 10px;
	float: right;
	margin-right: 3px;
	opacity: .7;
}

.btn-m .loader_bar {
	width: 3px;
	height: 7px;
	margin-right: 2px;
}

.table-container .loader {
	margin-top: 30px;
}

.table-container .loader_bar {
	background-color: #437CEC;
}

.notice {
	width: 100%;
	font-size: 12px;
	background-color: #fff0f0;
	color: #C31612;
	padding: 15px;
	line-height: 1.5;
	border: 1px solid #ef504c;
	box-sizing: border-box;
}

.notice h5 {
	margin-bottom: 5px;
}

.notice.warning {
	background-color: #fff8db;
	color: #b58105;
	border-color: #F5C248;
}

.notice-tag {
	float: right;
	background: #F5C248;
	padding: 5px 15px;
	color: #483300;
	margin-top: -15px;
	margin-right: -15px;
}